import {login} from '../../utils/api.js'
export default {
	data() {
		return {
			loading:false,
			userCode:'',
			userPassword:'',
			userDw:'',
			userMessage:'',
			times:'发送验证码',
			isSend:false,
			seconds:null,
		}
	},
	onLoad() {
		
	},
	methods: {
		submit(){
			console.log(this.userCode)
		},
		sendCode(){
			if(this.isSend) return false
			this.seconds = 60
			let timeOut = setInterval(() =>{
				this.isSend=true
				this.seconds--;
				this.times=`${this.seconds}s后重新发送`
				if(this.seconds==0){
					clearInterval(timeOut)
					this.isSend=false
					this.times="发送验证码"  
				}
			},1000)
		},
	},
	
}
